"use client";

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { useUser } from '@/context/UserContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Separator } from '@/components/ui/separator';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';

interface CourseDetail {
  id: string;
  title: string;
  description: string;
  duration: string;
  fee: string;
  schedule: string;
  modules: string[];
  outline: string;
  lectureOne: string;
}

interface CourseDetailPageProps {
  params: {
    courseId: string;
  };
}

export default function CourseDetailPage({ params }: CourseDetailPageProps) {
  const { user, logout, loading: userLoading } = useUser();
  const [course, setCourse] = useState<CourseDetail | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const router = useRouter();

  useEffect(() => {
    if (!userLoading && !user) {
      router.push('/login');
      return;
    }

    if (user && user.role === 'student' && !user.approved) {
      router.push('/dashboard');
      return;
    }

    // Fetch course details
    const fetchCourse = async () => {
      try {
        const response = await fetch(`/api/courses/${params.courseId}`);
        if (response.ok) {
          const data = await response.json();
          setCourse(data.course);
        } else if (response.status === 404) {
          setError('Course not found');
        } else {
          setError('Failed to load course details');
        }
      } catch (err) {
        setError('Error loading course details');
      } finally {
        setLoading(false);
      }
    };

    if (user) {
      fetchCourse();
    }
  }, [user, userLoading, router, params.courseId]);

  const handleLogout = () => {
    logout();
    router.push('/');
  };

  const formatContent = (content: string) => {
    return content.split('\n').map((line, index) => {
      const trimmedLine = line.trim();
      
      if (trimmedLine.startsWith('# ')) {
        return <h1 key={index} className="text-3xl font-bold text-black mt-6 mb-4">{trimmedLine.substring(2)}</h1>;
      } else if (trimmedLine.startsWith('## ')) {
        return <h2 key={index} className="text-2xl font-semibold text-black mt-5 mb-3">{trimmedLine.substring(3)}</h2>;
      } else if (trimmedLine.startsWith('### ')) {
        return <h3 key={index} className="text-xl font-semibold text-black mt-4 mb-2">{trimmedLine.substring(4)}</h3>;
      } else if (trimmedLine.startsWith('- ')) {
        return <li key={index} className="text-gray-700 ml-4">{trimmedLine.substring(2)}</li>;
      } else if (trimmedLine.match(/^\d+\./)) {
        return <li key={index} className="text-gray-700 ml-4 list-decimal">{trimmedLine}</li>;
      } else if (trimmedLine === '') {
        return <br key={index} />;
      } else {
        return <p key={index} className="text-gray-700 mb-2">{trimmedLine}</p>;
      }
    });
  };

  if (userLoading || loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <p className="text-gray-600">Loading course details...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return null; // Will redirect to login
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-50">
        <header className="bg-black text-white py-4">
          <div className="container mx-auto px-4 flex justify-between items-center">
            <h1 className="text-2xl font-bold">The Fourth Sealing Hub</h1>
            <Button 
              onClick={handleLogout}
              variant="outline" 
              className="border-white text-white hover:bg-white hover:text-black"
            >
              Logout
            </Button>
          </div>
        </header>
        
        <main className="container mx-auto px-4 py-8">
          <Alert className="border-red-200 bg-red-50 max-w-2xl mx-auto">
            <AlertDescription className="text-red-800">
              {error}
            </AlertDescription>
          </Alert>
          <div className="text-center mt-6">
            <Link href="/dashboard">
              <Button className="bg-black text-white hover:bg-gray-800">
                Back to Dashboard
              </Button>
            </Link>
          </div>
        </main>
      </div>
    );
  }

  if (!course) {
    return null;
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-black text-white py-4">
        <div className="container mx-auto px-4 flex justify-between items-center">
          <div>
            <h1 className="text-2xl font-bold">The Fourth Sealing Hub</h1>
            <p className="text-gray-300">Course Details</p>
          </div>
          <div className="flex items-center space-x-4">
            <span className="text-sm">Welcome, {user.name}</span>
            <Button 
              onClick={handleLogout}
              variant="outline" 
              className="border-white text-white hover:bg-white hover:text-black"
            >
              Logout
            </Button>
          </div>
        </div>
      </header>

      {/* Navigation */}
      <nav className="bg-gray-100 py-3">
        <div className="container mx-auto px-4">
          <div className="flex items-center space-x-2 text-sm">
            <Link href="/dashboard" className="text-black hover:underline">
              Dashboard
            </Link>
            <span className="text-gray-500">/</span>
            <span className="text-gray-700">Course Details</span>
          </div>
        </div>
      </nav>

      <main className="container mx-auto px-4 py-8">
        {/* Course Header */}
        <div className="mb-8">
          <h2 className="text-4xl font-bold text-black mb-4">{course.title}</h2>
          <p className="text-xl text-gray-600 mb-6">{course.description}</p>
          
          {/* Course Info Cards */}
          <div className="grid md:grid-cols-3 gap-4 mb-6">
            <Card className="border-2 border-gray-200">
              <CardHeader className="pb-3">
                <CardTitle className="text-lg text-black">Duration</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-gray-700 font-medium">{course.duration}</p>
              </CardContent>
            </Card>
            
            <Card className="border-2 border-gray-200">
              <CardHeader className="pb-3">
                <CardTitle className="text-lg text-black">Fee</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-gray-700 font-medium">{course.fee}</p>
              </CardContent>
            </Card>
            
            <Card className="border-2 border-gray-200">
              <CardHeader className="pb-3">
                <CardTitle className="text-lg text-black">Schedule</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-gray-700 font-medium text-sm">{course.schedule}</p>
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Course Content Tabs */}
        <Tabs defaultValue="overview" className="w-full">
          <TabsList className="grid w-full grid-cols-4 mb-6">
            <TabsTrigger value="overview">Overview</TabsTrigger>
            <TabsTrigger value="modules">Modules</TabsTrigger>
            <TabsTrigger value="outline">Course Outline</TabsTrigger>
            <TabsTrigger value="lecture">Lecture 1</TabsTrigger>
          </TabsList>
          
          <TabsContent value="overview">
            <Card className="border-2 border-gray-200">
              <CardHeader>
                <CardTitle className="text-2xl text-black">Course Overview</CardTitle>
                <CardDescription>
                  Comprehensive information about this course
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <h3 className="text-lg font-semibold text-black mb-2">About This Course</h3>
                  <p className="text-gray-700">{course.description}</p>
                </div>
                
                <Separator />
                
                <div>
                  <h3 className="text-lg font-semibold text-black mb-2">Course Details</h3>
                  <div className="grid md:grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm text-gray-600">Duration</p>
                      <p className="font-medium text-black">{course.duration}</p>
                    </div>
                    <div>
                      <p className="text-sm text-gray-600">Investment</p>
                      <p className="font-medium text-black">{course.fee}</p>
                    </div>
                    <div className="md:col-span-2">
                      <p className="text-sm text-gray-600">Schedule</p>
                      <p className="font-medium text-black">{course.schedule}</p>
                    </div>
                  </div>
                </div>

                <Separator />

                <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
                  <h4 className="font-semibold text-yellow-800 mb-2">Important Notice</h4>
                  <p className="text-sm text-yellow-700">
                    Course content is for viewing only and cannot be downloaded. All materials remain 
                    the intellectual property of The Fourth Sealing Hub.
                  </p>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
          
          <TabsContent value="modules">
            <Card className="border-2 border-gray-200">
              <CardHeader>
                <CardTitle className="text-2xl text-black">Course Modules</CardTitle>
                <CardDescription>
                  Detailed breakdown of what you'll learn
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {course.modules.map((module, index) => (
                    <div key={index} className="flex items-start space-x-3 p-3 bg-gray-50 rounded-lg">
                      <div className="bg-black text-white rounded-full w-8 h-8 flex items-center justify-center text-sm font-bold">
                        {index + 1}
                      </div>
                      <div>
                        <h4 className="font-semibold text-black">{module}</h4>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </TabsContent>
          
          <TabsContent value="outline">
            <Card className="border-2 border-gray-200">
              <CardHeader>
                <CardTitle className="text-2xl text-black">Detailed Course Outline</CardTitle>
                <CardDescription>
                  Week-by-week breakdown of the curriculum
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="prose max-w-none">
                  {formatContent(course.outline)}
                </div>
              </CardContent>
            </Card>
          </TabsContent>
          
          <TabsContent value="lecture">
            <Card className="border-2 border-gray-200">
              <CardHeader>
                <CardTitle className="text-2xl text-black">Lecture 1: Introduction</CardTitle>
                <CardDescription>
                  First lecture content - Introduction to the course
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="prose max-w-none" style={{ userSelect: 'text' }}>
                  {formatContent(course.lectureOne)}
                </div>
                
                <Separator className="my-6" />
                
                <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
                  <h4 className="font-semibold text-blue-800 mb-2">Note</h4>
                  <p className="text-sm text-blue-700">
                    This is a sample of the course content. Full course materials will be available 
                    after enrollment and payment confirmation.
                  </p>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>

        {/* Action Buttons */}
        <div className="mt-8 flex justify-between items-center">
          <Link href="/dashboard">
            <Button variant="outline" className="border-black text-black hover:bg-black hover:text-white">
              Back to Dashboard
            </Button>
          </Link>
          
          <div className="text-right">
            <p className="text-sm text-gray-600 mb-2">
              Interested in enrolling?
            </p>
            <p className="text-sm text-black font-medium">
              Contact: thefourthsealing@outlook.com
            </p>
          </div>
        </div>
      </main>
    </div>
  );
}
